
import React from 'react';
import './create-skill-item.css';

const  CreateSkillItem =(adress,name)=>{
  return(
    <li className="create-skill-item ">
      <img src={adress} alt={name} width="104"  height="96"/>
      <h6>{name}</h6>
    </li>
  )
}

export default CreateSkillItem;
