import React from 'react';
import './skills.css';
import CreateSkillItem from '../create-skill-item/create-skill-item.js';

const Skills  =()=>{
  return  (
    <div className='container skills'>
    <h3>Основной стек технологий</h3>
      <ul className="skills-list">
        { CreateSkillItem(require('../create-skill-item/image/bootstrap4.png'),'Bootstrap 4') }
        { CreateSkillItem(require('../create-skill-item/image/react.png'),'React') }
        { CreateSkillItem(require('../create-skill-item/image/webpack.png'),'Webpack') }
        { CreateSkillItem(require('../create-skill-item/image/html5.png'),'HTML 5') }
        { CreateSkillItem(require('../create-skill-item/image/css3.png'),'CSS 3') }
        { CreateSkillItem(require('../create-skill-item/image/redux.png'),'Redux') }
        { CreateSkillItem(require('../create-skill-item/image/babel.png'),'Babel') }
        { CreateSkillItem(require('../create-skill-item/image/node.png'),'Node') }

      </ul>
    </div>
  )
}

export default Skills;
