import React from 'react';
import './page.css';
import AboutMe from '../about-me/about-me'
import Avatar from '../avatar/avatar'
import Logo from '../logo/logo'
import MyAchievement from '../my-achievement/my-achievement'
import Skills from '../skills/skills'
import MoreSkills from '../more-skills/more-skills'

const Page =()=>{
  return (
    <>
      <div className='container'>
        <div className="row  align-content-center">
          <div className="col-lg-9 page" >
            <div className="row justify-content-space-between">

            <div className="col-md-4 px-lg-4 justify-content-">
              <Avatar/>
            </div>

            <div className="col-md-8 px-lg-2 justify-content-center">
              <Logo className="scale"/>
            </div>

              <div className="col-md-4 px-lg-4">
                <AboutMe/>
              </div>

                <div className="col-md-7 px-lg-2">
                  <Skills/>
                </div>

                <div className="col-md-4 px-lg-4">
                  <MyAchievement/>
                </div>

                <div className="col-md-7 px-lg-0">
                  <MoreSkills/>
                </div>

            </div>
          </div>
        </div>
      </div>
    </>
  );
}
export default Page;
